"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { startRun } from "@/lib/api";

export default function NewRunPage() {
  const router = useRouter();
  const [isStarting, setIsStarting] = useState(false);
  const [error, setError] = useState<string | null>(null);

  async function handleStartRun() {
    try {
      setIsStarting(true);
      setError(null);
      
      // Start the run
      const result = await startRun();
      
      // Redirect based on the run ID
      if (result.run_id === '') {
        // If we got a placeholder ID, go to the runs list page
        router.push('/runs');
      } else {
        // Redirect to the specific run details page
        router.push(`/runs/${result.run_id}`);
      }
    } catch (err: any) {
      setError(err.message || "Failed to start run. Please try again later.");
      console.error(err);
    } finally {
      setIsStarting(false);
    }
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-3xl font-bold tracking-tight">Start New Run</h1>
        <Link href="/runs">
          <Button variant="outline">Cancel</Button>
        </Link>
      </div>

      {error && (
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      <Card>
        <CardHeader>
          <CardTitle>Run Configuration</CardTitle>
          <CardDescription>
            Configure the parameters for the new honeypot testing run
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-8">
            <div className="grid gap-6 md:grid-cols-2">
              <div>
                <h3 className="text-lg font-medium mb-4">Attacker Model</h3>
                <div className="space-y-2">
                  <div className="rounded-md bg-muted p-4">
                    <div className="text-sm font-medium">Provider</div>
                    <div className="text-2xl">OpenAI</div>
                  </div>
                  <div className="rounded-md bg-muted p-4">
                    <div className="text-sm font-medium">Model</div>
                    <div className="text-2xl">GPT-4</div>
                  </div>
                </div>
              </div>
              
              <div>
                <h3 className="text-lg font-medium mb-4">Defender Model</h3>
                <div className="space-y-2">
                  <div className="rounded-md bg-muted p-4">
                    <div className="text-sm font-medium">Provider</div>
                    <div className="text-2xl">OpenAI</div>
                  </div>
                  <div className="rounded-md bg-muted p-4">
                    <div className="text-sm font-medium">Model</div>
                    <div className="text-2xl">GPT-4</div>
                  </div>
                </div>
              </div>
            </div>
            
            <div>
              <h3 className="text-lg font-medium mb-4">Run Settings</h3>
              <div className="grid gap-4 md:grid-cols-3">
                <div className="rounded-md bg-muted p-4">
                  <div className="text-sm font-medium">Iterations</div>
                  <div className="text-2xl">5</div>
                </div>
                <div className="rounded-md bg-muted p-4">
                  <div className="text-sm font-medium">Attack Chains</div>
                  <div className="text-2xl">Enabled</div>
                </div>
                <div className="rounded-md bg-muted p-4">
                  <div className="text-sm font-medium">Reasoning Request</div>
                  <div className="text-2xl">Always</div>
                </div>
              </div>
            </div>
            
            <div className="pt-4 flex justify-between items-center">
              <div>
                <Link href="/config">
                  <Button variant="outline">Modify Configuration</Button>
                </Link>
              </div>
              <div>
                <Button 
                  disabled={isStarting} 
                  onClick={handleStartRun}
                  className="w-40"
                >
                  {isStarting ? (
                    <>
                      <span className="animate-spin mr-2">‚ü≥</span>
                      Starting...
                    </>
                  ) : "Start Run"}
                </Button>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
} 